@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: #f9f9f9;
}

.Content {
  background-color: white;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: grey;
}

.Auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Auth-item {
  margin: 1em;
}

/* TOPBAR */
.TopBar-container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
}

.TopBar-item {
  margin: auto; /* Magic! */
}

.rowTd {
  border: 1px solid #ddd;
  color: black;
  background: #f9f9f9;
}

.popup {  
  position: fixed;  
  width: 100%;  
  height: 100%;  
  top: 0;  
  left: 0;  
  right: 0;  
  bottom: 0;  
  margin: auto;  
  background-color: rgba(0,0,0, 0.5);  
}  
.popup\_inner {  
  position: absolute;  
  left: 25%;  
  right: 25%;  
  top: 25%;  
  bottom: 25%;  
  margin: auto;  
  border-radius: 20px;  
  background: white;  
}
/* TABLE */
.Actions-List {
  border-collapse: collapse;
}
.Actions-tr td {
  color: black;
  background-color: white;
}

